html {
  scroll-behavior: smooth;
}

body {
  letter-spacing: .03em;
  min-width: 320px;
  cursor: default;
  font-family: DM Sans, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
  list-style: none;
}

img {
  height: auto;
  display: block;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 35px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 115px;
  }
}

.hero {
  height: 602px;
  z-index: 1;
  background-color: #ffb8ca;
  padding-top: 37px;
  padding-bottom: 60px;
  position: relative;
}

.hero .container {
  padding: 0 15px;
}

.hero-wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.hero-wrapper__list {
  justify-content: space-between;
  align-items: center;
  gap: 35px;
  display: flex;
}

.hero-wrapper__item {
  color: #fff;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  text-decoration: none;
}

.hero-wrapper__item:hover {
  cursor: pointer;
  color: #d41443;
}

.hero-btn {
  width: 159px;
  height: 44px;
  cursor: pointer;
  color: #d41443;
  background: #fff;
  border: none;
  border-radius: 22px;
  padding-right: 20px;
  font-weight: 500;
  transition: all .25s;
  top: 40px;
  left: 1025px;
}

.hero-btn:after, .hero-btn:before {
  content: "";
  width: 9px;
  height: 2px;
  background-color: #d41443;
  border-radius: 50px;
  position: absolute;
  top: 60px;
}

.hero-btn:after {
  transform: translate(10px)rotate(38deg);
}

.hero-btn:before {
  transform: translate(74px, 5px)rotate(-36deg);
}

.hero-btn:hover {
  background: #ecebeb;
}

.hero-content {
  justify-content: space-between;
  padding-top: 72px;
  padding-bottom: 60px;
  display: flex;
}

.hero-content__left {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.hero-content__title {
  max-width: 277px;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fff;
  font-family: Titan One;
  font-size: 38px;
  font-weight: 400;
  line-height: 44px;
}

.hero-content__title span {
  color: #d41443;
  font-family: Titan One;
  font-size: 3.5rem;
}

.hero-content__btns {
  gap: 12px;
  padding-top: 24px;
  display: flex;
}

.hero-content__btn {
  color: #fff;
  cursor: pointer;
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 50px;
  padding: 11px 23px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  transition: all .25s;
}

.hero-content__btn-active {
  color: #fff;
  background-color: #d41443;
}

.hero-content__btn:hover {
  color: #d41443;
  background-color: #ecebeb;
}

.hero-content__text {
  max-width: 200px;
  color: #fff;
  font-size: 14px;
  font-weight: 300;
  line-height: 140%;
  position: relative;
  transform: translateY(-54px);
}

.hero-content__text span {
  width: 26px;
  height: 26px;
  background-color: #fff;
  border-radius: 100%;
  display: block;
  position: absolute;
  top: 97px;
}

.hero-content__footer {
  align-items: flex-end;
  display: flex;
  position: relative;
}

.hero-content__footer-img {
  width: 213px;
  height: 216px;
  transform: translateY(10px);
}

.hero-content__footer-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-content__center {
  min-width: 534px;
  min-height: 534px;
  z-index: -1;
  background-color: #ffa3b9;
  border-radius: 100%;
  position: relative;
  transform: translate(9%, -19%);
}

.hero-content__center-img {
  width: 236px;
  height: 557px;
  display: block;
  position: absolute;
  top: 15%;
  left: 22%;
}

.hero-content__right-number {
  text-align: right;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #d41443;
  flex-direction: column;
  gap: 2px;
  padding-right: 12px;
  font-family: Titan One;
  font-size: 46px;
  font-style: normal;
  font-weight: 400;
  line-height: 53px;
  display: flex;
  position: relative;
}

.hero-content__right-number span {
  text-align: right;
  color: #fff;
  font-size: 7px;
  font-weight: 500;
  line-height: 18px;
}

.hero-content__right-circle {
  width: 30px;
  height: 30px;
  z-index: -1;
  background-color: #fff;
  border-radius: 100%;
  display: block;
  position: absolute;
  top: -8%;
  right: 0;
}

.hero-content__right-img {
  position: relative;
}

.hero-content__right-woman {
  position: absolute;
  top: 149px;
  right: -10px;
}

.menu {
  justify-content: space-between;
  align-items: space-between;
  flex-direction: column;
  gap: 5px;
  display: flex;
  position: absolute;
  top: 11%;
  left: 71%;
}

.menu-burger {
  width: 20px;
  border: .8px solid #d41443;
}

@media (max-width: 1199px) {
  .hero {
    height: 416px;
    padding-bottom: 28px;
  }

  .hero .container {
    max-width: 860px;
    padding: 0 20px;
  }

  .hero-wrapper__list {
    display: none;
  }

  .hero-content {
    padding-top: 14px;
    padding-bottom: 0;
  }

  .hero-content__left {
    gap: 20px;
  }

  .hero-content__title {
    max-width: 167px;
    font-size: 25px;
    line-height: 31px;
  }

  .hero-content__title span {
    font-size: 2.5rem;
  }

  .hero-content__btns {
    padding-top: 20px;
  }

  .hero-content__btn {
    padding: 4px 13px;
    font-size: 9px;
  }

  .hero-content__footer-img {
    max-height: 206px;
    max-width: 202px;
  }

  .hero-content__footer-img img {
    width: 80%;
    height: 80%;
    object-fit: contain;
  }

  .hero-content__text {
    max-width: 114px;
    font-size: 8px;
    transform: translate(-31px, -103px);
  }

  .hero-content__text span {
    top: 51px;
  }

  .hero-content__center {
    min-width: 361px;
    min-height: 335px;
    transform: translate(-26%, -19%);
  }

  .hero-content__center-img {
    height: 411px;
    top: 10%;
  }

  .hero-content__right-img {
    position: absolute;
    bottom: 176px;
  }

  .hero-content__right-woman {
    max-width: 209px;
    right: 0;
    top: unset;
  }

  .hero-content__right-woman img {
    width: 223px;
    position: absolute;
    right: -73px;
  }

  .hero-content__right-number {
    margin-bottom: 17px;
    font-size: 28px;
    line-height: 32px;
  }

  .hero-content__right-number span {
    font-size: 5px;
    line-height: 11px;
  }

  .hero-content__right-circle {
    width: 16px;
    height: 16px;
    top: -4%;
    left: 68%;
  }
}

@media (max-width: 768px) {
  .hero {
    padding-top: 8px;
    padding-bottom: 6px;
    overflow: hidden;
  }

  .hero-btn {
    display: none;
  }
}

@media (max-width: 768px) and (min-width: 769px) {
  .menu {
    visibility: hidden;
  }
}

@media (min-width: 1199px) {
  .menu {
    display: none;
  }
}

.products {
  background: #fff;
  padding: 60px 0;
}

.products__heading-label {
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
  margin: 0;
  padding-bottom: 15px;
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.products__heading {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #d41443;
  margin: 0;
  padding-bottom: 150px;
  font-family: Titan One;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 39px;
}

.products__list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 150px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.products__item {
  width: 280px;
  height: 489px;
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
  position: relative;
}

.products__item:first-child {
  background: #ffb8ca;
}

.products__item:nth-child(2) {
  background: #f0d1a5;
}

.products__item:last-child {
  background: #c2e297;
}

.products__item:first-child:before {
  content: "";
  width: 280px;
  height: 280px;
  background-image: url("icecream-M-@2x.57e1bf6d.png");
  background-size: 280px 280px;
  position: absolute;
  top: -150px;
  left: 50%;
  transform: translateX(-50%);
}

.products__item:nth-child(2):before {
  content: "";
  width: 280px;
  height: 280px;
  background-image: url("icecoffee-M-@2x.248a176c.png");
  background-size: 280px 280px;
  position: absolute;
  top: -150px;
  left: 50%;
  transform: translateX(-50%);
}

.products__item:last-child:before {
  content: "";
  width: 280px;
  height: 280px;
  background-image: url("milkshake-M-@2x.6dc0264c.png");
  background-size: 280px 280px;
  position: absolute;
  top: -150px;
  left: 50%;
  transform: translateX(-50%);
}

.products__name {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
  padding: 165px 0 30px;
  font-family: Titan One;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 23px;
}

.products__description {
  text-align: center;
  letter-spacing: .04em;
  color: #fff;
  width: 245px;
  height: 106px;
  margin: 0;
  padding: 30px 0 60px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 152%;
}

.products__icon {
  fill: #d41443;
  width: 40px;
  height: 40px;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 60px;
}

.mark {
  width: 6px;
  height: 6px;
  background-color: #fff;
  border-radius: 50%;
  transition: z-index .35s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.mark:before {
  top: 0;
  right: 10px;
}

.mark:after {
  top: 0;
  left: 10px;
}

.mark:before, .mark:after {
  content: "";
  width: inherit;
  height: inherit;
  background-color: inherit;
  box-sizing: border-box;
  border-radius: inherit;
  position: absolute;
}

