@media (min-width: 768px) {
  .products__heading-label {
    font-size: 30px;
    line-height: 34px;
    animation: animationY65 3s 1s infinite;
  }

  @keyframes animationY65 {
    0% {
      transform: translateY(0);
    }

    50% {
      transform: translateY(65px);
    }

    100% {
      transform: translateY(0);
    }
  }

  .products__heading {
    padding-bottom: 280px;
    font-size: 58px;
    line-height: 66px;
    animation: animateY65 3s 1s infinite;
