    }

    50% {
      transform: translateY(-65px);
    }

    100% {
      transform: translateY(0);
    }
  }

  .products__list {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    display: grid;
  }

  .products__item {
    width: 368px;
