    background-size: 368px 394px;
    top: -250px;
    left: 50%;
    transform: translateX(-50%);
  }

  .products__item:hover, .products__item:focus {
    animation-delay: cubic-bezier(.17, .67, .83, .67);
    transform: scale(1.1);
    box-shadow: 0 0 15px 10px rgba(255, 184, 202, .55);
  }

  .products__item:nth-child(2):hover, .products__item:nth-child(2):focus {
    box-shadow: 0 0 15px 10px rgba(240, 209, 165, .55);
  }

  .products__item:last-child:hover, .products__item:last-child:focus {
    box-shadow: 0 0 15px 10px rgba(194, 226, 151, .55);
  }

  .products__name {
    padding-top: 165px;
    font-size: 30px;
    line-height: 34px;
  }

  .products__description {
    width: 241px;
    height: 106px;
    font-size: 16px;
    line-height: 176%;
  }

  .products__icon:hover, .products__icon:focus {
    fill: #ffb8ca;
    cursor: pointer;
    background: #d41443;
    border-radius: 30%;
    transition: transform .3s;
    transform: rotate(-.25turn);
  }
}

