.modal {
  width: 100%;
  height: 100%;
  z-index: 599;
  background: rgba(255, 184, 202, .3);
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
}

.modal--is-hidden {
  visibility: hidden;
  opacity: 0;
}

.modal__inner {
  box-sizing: border-box;
  width: 280px;
  height: 490px;
  background: #ffb8ca;
  border: 2px solid #d41443;
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
